import DataTable from 'react-data-table-component';
import "./style.css"
const columns = [
    {
        name: 'Title',
        selector: row => row.title,
    },
    {
        name: 'Year',
        selector: row => row.year,
    },
];

const data = [
    {
        id: 1,
        title: 'Beetlejuice',
        year: '1988',
    },
    {
        id: 2,
        title: 'Ghostbusters',
        year: '1984',
    },
]

export default function index() {
    return (
        <>
            <div className="sidebar">
                <div className="logo-details">
                    <i className='bx '></i>
                    <span className="logo_name">PAN</span>
                </div>
                <ul className="nav-links">
                    <li>
                        <a href="#anggota" className="menu-item">
                            <i className='bx bxs-user-pin'></i>
                            <span className="links_name">Anggota</span>
                        </a>
                    </li>
                    <li>
                        <a href="#wilayah" className="menu-item">
                            <i className='bx bx-map-alt'></i>
                            <span className="links_name">Wilayah</span>
                        </a>
                    </li>
                    <li>
                        <a href="#gallery" className="menu-item">
                            <i className='bx bx-grid-alt'></i>
                            <span className="links_name">Gallery</span>
                        </a>
                    </li>
                    <li>
                        <a href="#suara" className="menu-item">
                            <i className='bx bx-bar-chart'></i>
                            <span className="links_name">Suara</span>
                        </a>
                    </li>
                    <li>
                        <a href="#news" className="menu-item">
                            <i className='bx bxs-news'></i>
                            <span className="links_name">News</span>
                        </a>
                    </li>
                    <li>
                        <a href="#user" className="menu-item">
                            <i className='bx bxs-user-check'></i>
                            <span className="links_name">User</span>
                        </a>
                    </li>
                </ul>
            </div>
            <section className="home-section">
                <nav>
                    <div className="sidebar-button">
                        <i className='bx bx-menu sidebarBtn'></i>
                        <span className="dashboard">Dashboard</span>
                    </div>
                    <div>
                        <a className="log_out" href="#">
                            <i className='bx bx-log-out'></i>
                            <span>Log out</span>
                        </a>
                    </div>

                </nav>

                <div className="home-content">

                    <div className="sales-boxes">
                        <div className="recent-anggota box">
                            <div className="title">Anggota</div>
                            <div className="button">
                                <a href="#">
                                    <i className='bx bx-log-out'></i>
                                    <span className="">Tambah Anggota</span>
                                </a>
                            </div>
                            <br/>
                                <div className="sales-details">
                                    <DataTable columns={columns} data={data}/>
                                </div>
                                <br/>
                                    <div className="pagination">
                                        <a href="#">&laquo;</a>
                                        <a href="#">1</a>
                                        <a className="active" href="#">2</a>
                                        <a href="#">3</a>
                                        <a href="#">4</a>
                                        <a href="#">5</a>
                                        <a href="#">6</a>
                                        <a href="#">&raquo;</a>
                                    </div>
                        </div>

                    </div>
                </div>
            </section>
        </>
    )
};