import Nav from "../Components/Nav";
import "./index.css";
const Tanah = () => {
  return (
    <>
      <div className="container-header">
        <Nav index={2} />
      </div>
      <div className="sejarah">
        <img src="logoPAN.png" alt="logo PAN" />
        <p>
          <b>Partai Amanat Nasional</b> menginginkan reformasi agraria, agar
          seluruh warga negara bisa memiliki akses terhadap tanah. Penguasaan
          berlebihan atas tanah mesti dibatasi. Pelaksanaan UU Pokok Agraria
          secara konsisten dan pengakuan hak ulayat, dapat menjadi langkah awal
          penataan tanah di Indonesia.
        </p>
      </div>
    </>
  );
};
export default Tanah;
