import Header from "../header/index";
import "./style.css"
export default function (){
    const search = (e)=>{
        let ser = document.getElementById('search').value
        if(ser.length > 0){
            document.getElementById("btnSearch").style.display = "block";
        }else{
            document.getElementById("btnSearch").style.display = "none";
        }
        console.log(ser)
    }
    return(
        <div className="main2">
            <Header/>
            <div className="headers">

                <form className="search" onSubmit="event.preventDefault();" role="search">
                    <input id="search" onChange={(e) => {search(e)}} id="search" type="search" placeholder="Search..." autoFocus required/>
                    <button id="btnSearch" type="submit">Go</button>
                </form>
            </div>
            <div className="content">
                <div className="map">

                </div>
            </div>

        </div>
    )
}