import "./style.css";
import Header from "../header";
import Nav from "../Components/Nav";
import { useEffect, useState } from "react";
import Auth from "../../api/auth";
import { getAccessToken, setAccessToken } from "../../utils/auth";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";
import GoogleLogin from 'react-google-login';

export default function Login() {
  let navigate = useNavigate();
  const [googleButton, setGoogleButton] = useState(<></>);

  useEffect(() => {
    if (getAccessToken()) {
      // navigate('/dashboard'); // redirect ke halaman dashboard apabila sudah login
    }

    Auth.getLogin().then((result) => {
      setGoogleButton(loginGoogleButton(result.data.google_client_id));
    }).catch((err) => {
      Swal.fire({
        icon: 'error',
        title: 'Oops..',
        text: 'Tidak dapat terhubung ke server'
      });
    })
  }, []);

  const handleLogin = () => {
    var email = document.getElementById("email").value;
    var password = document.getElementById("password").value;
    if (!email || !password) {
      Swal.fire({
        title: 'Oops...',
        text: 'Email atau password belum diisi',
        icon: 'error',
        timer: 2000
      });
      return;
    }
    Auth.loginWithEmail(email, password)
      .then((result) => {
        setAccessToken(result.data.access_token);
        navigate("/dashboard");
      })
      .catch((error) => {
        console.error(error);
        Swal.fire({
          title: "Oops...",
          text: "Email atau password salah",
          icon: "error",
        });
      });
  };

  const responseGoogleSuccess = (response) => {
    Auth.loginWithGoogle(response.tokenId).then(() => {
      Swal.fire({
        title: 'Success',
        text: 'Login berhasil',
        icon: 'success',
        timer: 1000
      }).then(() => {
        navigate("/dashboard");
      })
    }).catch((err) => {
      console.log(err.status_code);
      Swal.fire({
        icon: 'error',
        text: 'Maaf akun anda tidak terdaftar',
        title: 'Oops...'
      });
    });
  };

  const responseGoogleError = (reponse) => {

  }

  const loginGoogleButton = (client_id) => {
    return (
      <GoogleLogin
        clientId={`${client_id}`}
        render={renderProps => (
          <div onClick={renderProps.onClick} className="go" disabled={renderProps.disabled}>Google</div>
        )}
        buttonText="Login"
        onSuccess={responseGoogleSuccess}
        onFailure={responseGoogleError}
        cookiePolicy={'single_host_origin'}
      />
    )
  }

  return (
    <>
      <div className="main">
        <Header />
        <div className="background">
          <div className="shape" />
          <div className="shape" />
        </div>
        <form>
          <h3>Login Here</h3>

          <label htmlFor="email">Email</label>
          <input type="text" placeholder="Email" id="email" />

          <label htmlFor="password">Password</label>
          <input type="password" placeholder="Password" id="password" />

          <button onClick={handleLogin} type="button">
            Log In
          </button>
          <div className="social">

            {googleButton}
            {/* <div className="go">
              <i className="fab fa-google" /> Google
            </div> */}
            <div className="fb">
              <i className="fab fa-facebook" /> Facebook
            </div>
          </div>
        </form>
      </div>
    </>
  );
}
