import { useState } from "react";
import { Link } from "react-router-dom";
import {
  AiOutlineDown,
  AiOutlineRight,
  AiFillHome,
  AiTwotoneAppstore,
  AiOutlineCloseSquare,
} from "react-icons/ai";
import { BsPeopleFill, BsList } from "react-icons/bs";
import "./Nav.css";
const Nav = (props) => {
  const [platform, setPlatform] = useState({ toggle: false });
  const [dropdown, setDropdown] = useState(false);
  const handleToggle = () => {
    setPlatform((prevState) => ({ ...prevState, toggle: true }));
  };
  const handleLeave = () => {
    setPlatform({ toggle: false });
  };
  const handleDropdown = () => setDropdown(!dropdown);
  return (
    <>
      <ul className="nav">
        <div className="link">
          <div className="logo">
            <Link to="/">
              <img src="/logoPAN.png" alt="Logo" />
            </Link>
          </div>
          <Link to="/">
            <li className={props.index === 1 ? "link-txt active" : "link-txt"}>
              HOME
            </li>
          </Link>
          <div
            className={"dropdown active" + props.index === 2 ? "active" : ""}
            onMouseLeave={() => handleLeave()}
          >
            {/* {platform.toggle ? "dropdown-active" : "link-txt"} */}
            <li
              className={props.index === 2 ? "link-txt active" : "link-txt"}
              // onFocus={() => handleFocus()}
              onClick={handleToggle}
            >
              <div
                style={{ display: "flex", alignItems: "center", gap: ".5em" }}
              >
                <>PLATFORM</>
                {platform.toggle ? <AiOutlineDown /> : <AiOutlineRight />}
              </div>
            </li>
            <div
              className="popup"
              style={{ display: platform.toggle ? "" : "none" }}
            >
              <Link to="/sejarah">
                <div className="popup-link">Sejarah</div>
              </Link>
              <Link to="/prinsip">
                <div className="popup-link">Prinsip Dasar</div>
              </Link>
              <Link to="/politik">
                <div className="popup-link">Politik</div>
              </Link>
              <Link to="/pertahanan">
                <div className="popup-link">Pertahanan Negara</div>
              </Link>
              <Link to="/ekonomi">
                <div className="popup-link">Ekonomi</div>
              </Link>
              <Link to="/tanah">
                <div className="popup-link">Tanah</div>
              </Link>
              <Link to="/buruh">
                <div className="popup-link">Buruh</div>
              </Link>
              <Link to="/sosial">
                <div className="popup-link">Sosial dan Kemasyarakatan</div>
              </Link>
              <Link to="/pendidikan">
                <div className="popup-link">Pendidikan</div>
              </Link>
              <Link to="/lingkungan">
                <div className="popup-link">Lingkungan Hidup</div>
              </Link>
              <Link to="/pergaulan">
                <div className="popup-link">Pergaulan Dunia</div>
              </Link>
              <Link to="/jalan">
                <div className="popup-link">Jalan Kita</div>
              </Link>
            </div>
          </div>
          <Link to="/caleg">
            <li className={props.index === 3 ? "link-txt active" : "link-txt"}>
              CALON LEGISLATIF
            </li>
          </Link>
        </div>
        <div className="login">
          <Link to="/login">
            <button className="button">LOGIN</button>
          </Link>
        </div>
      </ul>
      <ul className="nav-mobile">
        <div className="logo">
          {dropdown ? (
            <h1>MENU</h1>
          ) : (
            <Link to="/">
              <img src="/logoPAN.png" alt="Logo" />
            </Link>
          )}
        </div>
        <div className="nav-dropdown" onClick={() => handleDropdown()}>
          {dropdown ? <AiOutlineCloseSquare /> : <BsList />}
        </div>
      </ul>
      <div
        className="nav-dropdown-content"
        style={{
          transform: `scale(${dropdown ? "1" : "0"})`,
          animation: dropdown ? "nav-dropdown 0.2s" : "",
        }}
      >
        <Link to="/">
          <p>
            <AiFillHome />
            Home
          </p>
        </Link>
        <Link to="/caleg">
          <p>
            <BsPeopleFill />
            Calon Legislatif
          </p>
        </Link>
        <ul>
          <p>
            <AiTwotoneAppstore />
            PLATFORM
          </p>
          <Link to="/sejarah">
            <li>Sejarah</li>
          </Link>
          <Link to="/prinsip">
            <li>Prinsip Dasar</li>
          </Link>
          <Link to="/politik">
            <li>Politik</li>
          </Link>
          <Link to="/pertahanan">
            <li>Pertahanan Negara</li>
          </Link>
          <Link to="/ekonomi">
            <li>Ekonomi</li>
          </Link>
          <Link to="/tanah">
            <li>Tanah</li>
          </Link>
          <Link to="/buruh">
            <li>Buruh</li>
          </Link>
          <Link to="/sosial">
            <li>Sosial dan Kemasyarakatan</li>
          </Link>
          <Link to="/pendidikan">
            <li>Pendidikan</li>
          </Link>
          <Link to="/lingkungan">
            <li>Lingkungan Hidup</li>
          </Link>
          <Link to="/pergaulan">
            <li>Pergaulan Dunia</li>
          </Link>
          <Link to="/jalan">
            <li>Jalan Kita</li>
          </Link>
        </ul>
        <Link to="/login">
          <button className="dropdown-button">LOGIN</button>
        </Link>
      </div>
    </>
  );
};
Nav.defaultProps = {
  index: 1,
};
export default Nav;
