import { useState } from "react";
import Nav from "../Components/Nav";
import "./index.css";
const Index = () => {
  const calon = [
    {
      nama: "Muawanah",
      no: "12",
      alamat: "alamat",
      pekerjaan: "pekerjaan",
      pesan: "pesan",
      url: "pan.png",
    },
    {
      nama: "Maliki",
      no: "02",
      alamat: "alamat",
      pekerjaan: "pekerjaan",
      pesan: "pesan",
      url: "pan.png",
    },
    {
      nama: "Taufik",
      no: "08",
      alamat: "alamat",
      pekerjaan: "pekerjaan",
      pesan: "pesan",
      url: "pan.png",
    },
  ];
  const [preview, setPreview] = useState(false);
  const [index, setIndex] = useState(0);
  const togglePreview = (index) => {
    setPreview(!preview);
    setIndex(index);
  };
  return (
    <>
      <div className="container-header">
        <Nav index={3} />
      </div>
      <div className="caleg">
        {preview ? (
          <div className="card-detail">
            <div className="detail-img">
              <img src={calon[index].url} alt={"detail" + [index]} />
            </div>
            <div className="detail">
              <table className="detail-table">
                <tr>
                  <td className="subjek">Nama</td>
                  <td>
                    {": "}
                    {calon[index].nama}
                  </td>
                </tr>
                <tr>
                  <td className="subjek">Alamat</td>
                  <td>
                    {": "}
                    {calon[index].alamat}
                  </td>
                </tr>
                <tr>
                  <td className="subjek">Pekerjaan</td>
                  <td>
                    {": "}
                    {calon[index].pekerjaan}
                  </td>
                </tr>
                <tr>
                  <td className="subjek">Pesan</td>
                  <td>
                    {": "}
                    {calon[index].pesan}
                  </td>
                </tr>
              </table>
              <button className="close-preview" onClick={() => togglePreview()}>
                Tutup
              </button>
            </div>
          </div>
        ) : (
          calon.map((item, index) => (
            <div className="card" onClick={() => togglePreview(index)}>
              <img src={calon[index].url} alt={"anggota" + [index]} />
              <div className="nomer">
                <p>{item.no}</p>
              </div>
              <div className="nama">{item.nama}</div>
            </div>
          ))
        )}
      </div>
    </>
  );
};
export default Index;
