import Nav from "../Components/Nav";
import "./index.css";
const Pendidikan = () => {
  return (
    <>
      <div className="container-header">
        <Nav index={2} />
      </div>
      <div className="sejarah">
        <img src="logoPAN.png" alt="logo PAN" />
        <p>
          Wajib belajar diterapkan untuk semua anak usia sekolah.
          <b>Partai Amanat Nasional </b>memberikan perhatian khusus terhadap
          pendidikan, agar generasi muda yang berkualitas bermunculan untuk
          mengemban tanggung-jawab masa depan bangsa. Alokasi dana pendidikan
          senantiasa ditingkatkan, agar para siswa dapat dibebaskan dari biaya
          sekolah dan segala pungutan yang memberatkan. Sistem pendidikan
          nasional harus bisa merangsang tumbuhnya akhlak yang baik, dan
          merangsang kemandirian serta kreativitas.
        </p>
        <p>
          <b>Partai Amanat Nasional</b> juga mendorong kegiatan penelitian
          ilmiah. Kegiatan itu mesti dilakukan secara bebas dan hasilnya terbuka
          untuk umum. Partai ini mencegah disalahgunakannya hasil penelitian
          untuk merusak umat manusia.
        </p>
      </div>
    </>
  );
};
export default Pendidikan;
