import "./App.css";
import { BrowserRouter, Routes, Route, NavLink } from "react-router-dom";

import Home from "./Pages/Home/Home";
import Login from "./Pages/login";
import Dashboard from "./Pages/dashboard";
import Caleg from "./Pages/Caleg";
import Admin from "./Pages/admin";
// Platform
import Sejarah from "./Pages/Platform/Sejarah";
import Prinsip from "./Pages/Platform/Prinsip";
import Politik from "./Pages/Platform/Politik";
import Pertahanan from "./Pages/Platform/Pertahanan";
import Ekonomi from "./Pages/Platform/Ekonomi";
import Tanah from "./Pages/Platform/Tanah";
import Buruh from "./Pages/Platform/Buruh";
import Sosial from "./Pages/Platform/Sosial";
import Pendidikan from "./Pages/Platform/Pendidikan";
import Lingkungan from "./Pages/Platform/Lingkungan";
import Pergaulan from "./Pages/Platform/Pergaulan";
import Jalan from "./Pages/Platform/Jalan";

function App() {
  return (
    <>
      <BrowserRouter>
        <Routes>
          <Route exact path="/" element={<Home />} />
          <Route path="/caleg" element={<Caleg />} />
          <Route path="/login" element={<Login />} />
          <Route path="/dashboard" element={<Dashboard />} />
          <Route path="/admin/dashboard" element={<Admin />} />
          {/* Platform */}
          <Route path="/sejarah" element={<Sejarah />} />
          <Route path="/prinsip" element={<Prinsip />} />
          <Route path="/politik" element={<Politik />} />
          <Route path="/pertahanan" element={<Pertahanan />} />
          <Route path="/ekonomi" element={<Ekonomi />} />
          <Route path="/tanah" element={<Tanah />} />
          <Route path="/buruh" element={<Buruh />} />
          <Route path="/sosial" element={<Sosial />} />
          <Route path="/pendidikan" element={<Pendidikan />} />
          <Route path="/lingkungan" element={<Lingkungan />} />
          <Route path="/pergaulan" element={<Pergaulan />} />
          <Route path="/jalan" element={<Jalan />} />
        </Routes>
      </BrowserRouter>
    </>
  );
}

export default App;
