import Nav from "../Components/Nav";
import "./index.css";
const Pertahanan = () => {
  return (
    <>
      <div className="container-header">
        <Nav index={2} />
      </div>
      <div className="sejarah">
        <img src="logoPAN.png" alt="logo PAN" />
        <p>
          Pertahanan negara merupakan usaha segenap masyarakat untuk
          mempertahankan tanah air. Perlindungan penduduk sipil merupakan bagian
          terpenting dari pertahanan negara.
        </p>
        <p>
          <b>Partai Amanat Nasional</b> berpendirian bahwa TNI harus tunduk pada
          hukum, konstitusi, dan berada di bawah kontrol publik. TNI yang
          berfungsi sebagai alat negara untuk menjaga keamanan negara, dan tidak
          mencampuri apalagi mendominasi urusan politik, ekonomi dan sosial.
          Sedangkan Kepolisian bertindak sebagai aparat penegak hukum,
          ketertiban, dan keamanan.
        </p>
      </div>
    </>
  );
};
export default Pertahanan;
