import Nav from "../Components/Nav";
import "./Home.css";


const Home = () => {

  return (
    <>
      <div className="container-header full-height">
        <Nav />

        <div className="hero">
          <div className="hero-section">
            <div className="judul">
              <h1>PARTAI AMANAT NASIONAL</h1>
              <p>Dewan Pimpinan Daerah Jombang</p>
            </div>
            <div className="info">
              <blockquote>
                Partai ini pada dasarnya adalah partai terbuka, meski sebagian
                orang menganggapnya partai orang Muhammadiyah karena sosok Amien
                Rais pada saat itu adalah Ketua umum Muhammadiyah
              </blockquote>
            </div>
          </div>
          <div className="avatar">
            <img src="./pan.png" alt="ketua PAN" />
          </div>
        </div>
      </div>
      <div className="home-label">
        <div id="label-layer">
          <h1>KONTRIBUSI PARTAI AMANAT NASIONAL</h1>
        </div>
      </div>
      <div className="galery-container">
        <div className="galery"></div>
        <p>
          Sesuai dengan visi misi dari Partai Amanat Nasional, Dewan Pimpinan
          Daerah Jombang menjalankan banyak kontribusi bagi masyarakat yang
          membutuhkan bantuan.
        </p>
      </div>
      <div className="profile">
        <div className="profile-section">
          <h1>H. DUKHA</h1>
          <p>KETUA DPD PAN JOMBANG</p>
          <span id="profile-section-split"></span>
          <p>
            Pada tanggal 12 Maret 2020, H. DHUKA terpilih menjadi ketua DPD
            Partai Amanat Nasional dalam kongres ke-V Partai Amanat Nasional. H.
            DHUKA didaulat oleh seluruh pemilik Hak Suara untuk menjadi Ketua
            Umum Partai Amanat Nasional Periode 2020-2025{" "}
          </p>
        </div>
        <div className="profile-pic">
          <img src="profil.png" alt="profile-pic" />
        </div>
      </div>
    </>
  );
};
export default Home;
