import Nav from "../Components/Nav";
import "./index.css";
const Lingkungan = () => {
  return (
    <>
      <div className="container-header">
        <Nav index={2} />
      </div>
      <div className="sejarah">
        <img src="logoPAN.png" alt="logo PAN" />
        <p>
          <b>Partai Amanat Nasional</b> memperjuangkan dilindunginya lingkungan
          hidup. Partai ini berkeyakinan bahwa lingkungan hidup adalah pinjaman
          dari generasi mendatang, yang mesti dilindungi dari keserakahan
          manusia.
        </p>
      </div>
    </>
  );
};
export default Lingkungan;
