import "./style.css";
import { Link } from "react-router-dom";
import { BsList } from "react-icons/bs";
import { BsXLg } from "react-icons/bs";
import { IconContext } from "react-icons";
import { useState } from "react";
const Index = () => {
  const [toggle, setToggle] = useState(false);
  const handleToggle = () => {
    setToggle(!toggle);
    if(toggle){
      document.getElementById("headers").style.display = "block";
    }else{
      document.getElementById("headers").style.display="none";
    }
  };
  return (
    <div className="Header">
      <input type="checkbox" id="active" checked={toggle} />
      <div className="header-top">
        <div className="logo" style={{ backgroundColor: "transparent" }}>
          <Link to="/">
            <img src="/logo PAN.jpg" alt="Logo" />
          </Link>
        </div>
        <label htmlFor="active">
          <IconContext.Provider value={{ size: "30" }}>
            <BsList onClick={() => handleToggle()} />
          </IconContext.Provider>
        </label>
      </div>
      {/* <label htmlFor="active" className="close"></label> */}
      <div className="wrapper">
        <div className="close-button">
          <IconContext.Provider value={{ size: "30",color:"white" }}>
            <BsXLg onClick={() => handleToggle()} />
          </IconContext.Provider>
        </div>
        <ul>
          <li>
            <a href="#">Home</a>
          </li>
          <li>
            <a href="#">About</a>
          </li>
          <li>
            <a href="#">Services</a>
          </li>
          <li>
            <a href="#">Gallery</a>
          </li>
          <li>
            <a href="#">Feedback</a>
          </li>
        </ul>
      </div>

    </div>
  );
};
export default Index;
